  padding: 0 5px * @m;
  margin-bottom: 30px * @m;
}

// 直播状态小圆点
.liveStatus span {
  display: inline-block;
  width: 16px * @m;
  height: 16px * @m;
  border-radius: 100%;
  margin-right: 10px * @m;
}

.liveStatus.ing span {
  background-image: linear-gradient(-180deg, #77ffa6 1%, #4bdb9a 100%);
  box-shadow: 0 8px * @m 10px * @m 0 rgba(118, 204, 251, 0.5),
